"use client";

import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";

export default function Hero() {
  const ref = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start start", "end start"],
  });

  // Expansion completes quickly
  const inset = useTransform(scrollYProgress, [0, 0.2], [72, 0]);
  const radius = useTransform(scrollYProgress, [0, 0.2], [40, 0]);
  const textScale = useTransform(scrollYProgress, [0, 0.2], [1, 1.12]);
  const textOpacity = useTransform(scrollYProgress, [0, 0.2, 0.35], [1, 0.15, 0]);
  const textBlur = useTransform(scrollYProgress, [0, 0.2, 0.35], [0, 3, 8]);
  const textBlurPx = useTransform(textBlur, (v: number) => `blur(${v}px)`);

  return (
    <div ref={ref} className="h-[108vh]">
      <div className="sticky top-0 h-screen">
        {/* Animated padding creates perfectly even bezels on all sides */}
        <motion.div style={{ padding: inset }} className="h-full w-full">
          <motion.div
            style={{ borderRadius: radius }}
            className="h-full w-full bg-[#1A3D5E] flex items-center justify-center"
          >
            <motion.div
              style={{ scale: textScale, opacity: textOpacity, filter: textBlurPx }}
              className="text-center text-white"
            >
              <h1 className="text-5xl md:text-6xl font-extrabold">Hey, I’m Nate.</h1>
              <p className="mt-3 text-lg md:text-xl opacity-90">And I make stuff.</p>
            </motion.div>
          </motion.div>
        </motion.div>
      </div>
    </div>
  );
}
